.faq {
	padding-top: 130px;
	padding-bottom: 10px;
	position: relative;
}

.faq__bg {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	z-index: -1;
	opacity: 0.8;
}

.faq__wrap {
	justify-content: space-between;
	align-items: flex-start;
}

.faq__h2 {
	font-family: 'Montserrat-bold';
	font-size: 96px;
	line-height: 117px;
	letter-spacing: -0.03em;
}

.faq__questions-list {
	width: 800px;
	position: relative;
	top: 10px;
	margin-bottom: 326px;
}

.faq__questions-item {
	padding-left: 90px;
	border-radius: 10px;
	padding-top: 30px;
	padding-bottom: 30px;
	padding-right: 50px;
	background: rgba(28, 30, 37, 0.8);
	position: relative;
	margin-bottom: 10px;
	backdrop-filter: blur(25px);
	transition: all 0.2s;
	&:last-child {
		margin-bottom: 0px;
	}
	&:hover {
		cursor: pointer;
		background: rgba(35, 37, 45, 0.8);
	}
}

.faq__questions-nav {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 2px solid #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	position: absolute;
	top: 30px;
	left: 30px;
	transition: all 0.2s;
}

.faq__questions-line {
	width: 10px;
	height: 2px;
	background-color: #fff;
	border-radius: 2px;
	position: relative;
	bottom: -1px;
	transition: all 0.2s;
}

.faq__questions-line:nth-child(2) {
	transform: rotate(90deg);
	bottom: 1px;
}


.faq__questions-title {
	font-family: 'Montserrat-bold';
	font-size: 22px;
	line-height: 32px;
	letter-spacing: 0em;
	text-align: left;
	margin-bottom: 0px;
	text-transform: uppercase;
}

.faq__questions-text {
	font-family: 'Montserrat-medium';
	font-weight: 500;
	font-size: 16px;
	line-height: 20px;
	letter-spacing: 0em;
	color: #fff;
	max-height: 0;
	transition: all 0.2s ease-out;
	overflow: hidden;
	-ms-overflow-style: none;
	scrollbar-width: none;
	opacity: 0;

	&::-webkit-scrollbar {
			width: 0;
			height: 0;
	}

	ul {
		padding-left: 30px;

		li {
			list-style: none;
			position: relative;
			&:before{
				width: 4px;
				height: 4px;
				content: '';
				display: block;
				background-color: #fff;
				border-radius: 50%;
				position: absolute;
				left: -17px;
				top: 13px;
			}
		}
	}

	a { text-decoration: underline !important; }
	p { margin-bottom: 20px; }
}

.faq__questions-item.active {
	.faq__questions-nav {
		opacity: 0.3;
	}
	.faq__questions-title {
		margin-bottom: 10px;
		background: linear-gradient(270deg, #387DFB 0%, #00F2FB 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		display: block;
	}
	.faq__questions-text {
		overflow: auto;
		max-height: 300px;
		opacity: 1;
	}
	.faq__questions-line:nth-child(2) {
		transform: rotate(0deg);
	}
}

@media (max-width: 1080px) {
	.faq{
		padding-top: 70px;
	}

	.faq__h2 {
		font-size: 60px;
		line-height: 66px;
		margin: 0 auto;
		margin-bottom: 50px;
	}
}

@media (max-width: 767px) {
	.faq{
		padding-top: 50px;
	}

	.faq__bg {
		height: 100%;
		top: 0;
		img {
			object-fit: cover;
		}
	}

	.faq__h2 {
		font-size: 36px;
		line-height: 44px;
		letter-spacing: -0.03em;
		margin: 0 auto;
		margin-bottom: 30px;
	}

	.faq__questions-list {
		margin-bottom: 50px;
	}

	.faq__questions-item {
		padding: 20px;
		padding-left: 70px;
		min-height: 70px;
	}

	.faq__questions-title {
		font-size: 14px;
		line-height: 17px;
		letter-spacing: 0em;
	}
	.faq__questions-text {
		font-size: 14px;
		line-height: 17px;
		letter-spacing: 0em;
		ul {
			li {

				&:before {
					top: 5px;
				}
			}
		}
	}

	.faq__questions-nav {
		top: 20px;
		left: 20px;
	}
}
